.popup {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  visibility: hidden;
  transition: visibility 0.3s, opacity 0.4s linear;
  z-index: 4;
}

.popup_opened {
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: visible;
  opacity: 1;
}

.popup__container {
  position: relative;
  justify-self: center;
  align-self: center;
  width: 700px;
  padding: 34px 36px 37px;
  background-color: #fff;
  box-sizing: border-box;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  background-color: #000;
  background-image: url(../../images/page-background.png);
  background-size: cover;
}

.popup__header {
  margin: 0;
  margin-top: 10px;
  margin-bottom: 10px;
  font-style: normal;
  font-weight: 900;
  font-size: 35px;
  line-height: 41px;
  font-family: 'Macherie';
  color: #2ec2f0;
  z-index: 100;
  position: relative;
  visibility: hidden;
}

.header_shown {
  visibility: visible;
  margin-top: 100px;
  margin-bottom: 83px;
}

.popup__close-button {
  display: block;
  position: absolute;
  top: 20px;
  right: 20px;
  margin: auto 0;
  width: 18px;
  height: 18px;
  background-image: url(../../images/Close-Icon.svg);
  background-color: transparent;
  background-size: cover;
  border: 0;
  cursor: pointer;
  z-index: 10;
  background-attachment: fixed;
  -o-background-size: cover;
  -moz-background-size: cover;
  -webkit-background-size: cover;
}

.popup__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  filter: brightness(0.6);
  z-index: 1;
}

.form {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  outline: none;
  justify-content: center;
  align-items: center;
  z-index: 10;
  position: relative;
}

.form__save-button {
  margin-top: 18px;
  margin-bottom: 100px;
  width: 197px;
  height: 30px;
  box-sizing: border-box;
  border: 2px solid #fff;
  border-radius: 2px;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #000;
  box-shadow: 0px 0px 5px 0.5px #fff, inset 0px 0px 4px 0.5px #fff;
  background-color: #fff;
  transition: 0.5s ease-in-out;
  display: none;
  z-index: 100;
}

.popup__save-button-visible {
  display: block;
}

.form__save-button_disabled {
  box-shadow: 0px 0px 5px 0.5px #fff, inset 0px 0px 4px 0.5px #fff;
  color: #fff;
  background-color: transparent;
}

input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

@media screen and (max-width: 960px) {
  .popup__container {
    width: 450px;
  }
  .popup__header {
    font-size: 25px;
    line-height: 29px;
  }
  .form__save-button {
    width: 180px;
    height: 24px;
    font-size: 12px;
    line-height: 14px;
  }
}

@media screen and (max-width: 460px) {
  .popup__container {
    width: 340px;
  }
}
